import React from 'react'
import { useForm } from "react-hook-form";

const Form = () => {
  const { register, handleSubmit, watch, formState: { errors } } = useForm();
  const onSubmit = data => console.log(data);
  console.log(watch("example"));
  
  return (
    <div>
      <form id='form' onSubmit={handleSubmit(onSubmit)}>
        <h2>    </h2>
        <div className='input-group mb-3'>  
          <input {...register('lastname', {required: true, 
           maxLength: 50, pattern: /^[a-zA-Z--]+$/i
          })}
          type="text" placeholder='' className='form-control'/>
        </div>
          {errors?.lastname?.type === 'required' && (<p>  </p>)}
          {errors?.lastname?.type === 'maxLength' && (<p> 50 </p>)}
          {errors?.lastname?.type === 'pattern' && (<p> </p>)}
        <div className='input-group mb-3'>  
          <input {...register('firstname', {required: true, 
           maxLength: 50, pattern: /^[a-zA-Z--]+$/i
          })}
          type="text" placeholder='' className='form-control'/>
        </div>
          {errors?.firstname?.type === 'required' && (<p>  </p>)}
          {errors?.firstname?.type === 'maxLength' && (<p> 50 </p>)}
          {errors?.firstname?.type === 'pattern' && (<p> </p>)}
        <div className='input-group mb-3'>  
          <input {...register('patronymic', {required: true, 
           maxLength: 50, pattern: /^[a-zA-Z--]+$/i
          })}
          type="text" placeholder='' className='form-control'/>
        </div>
          {errors?.patronymic?.type === 'required' && (<p>  </p>)}
          {errors?.patronymic?.type === 'maxLength' && (<p> 50 </p>)}
          {errors?.patronymic?.type === 'pattern' && (<p> </p>)}
        <div className='input-group mb-3'>  
          <input {...register('email', {required: true, 
           maxLength: 50})}
          type="text" placeholder=' ' className='form-control'/>
        </div>
          {errors?.email?.type === 'required' && (<p>  </p>)}
          {errors?.email?.type === 'maxLength' && (<p> 50 </p>)}
        <input className='btn btn-outline-primary' type="submit" />
      </form>
    </div>
  )
}

export default Form